.dropdown {
  z-index: 999;
  display: flex;
  justify-content: center;
  width: 160px;
}
.dropdown--hover:hover {
  color: #5643fd;
  cursor: pointer;
}
.dropdown--click {
  background-color: #5643fd;
  color: #fff;
  cursor: pointer;
}
.dropdown-title {
  display: flex;
  align-items: center;
}
.dropdown-title--icon {
  margin: 0px 7px;
  color: rgb(75, 96, 117);
}
.dropdown-items {
  position: absolute;
  margin-top: 50px;
  display: flex;
  flex-direction: column;
  background-color: #0c164f;
  border-radius: 4px;
}
.dropdown-items .dropdown-item {
  width: 160px;
  color: #fff;
  padding: 10px 12px;
}
.dropdown-items .dropdown-item:hover {
  color: #ffffff;
  background-color: #5643fd;
  border-radius: 4px;
}
